/* General page organization */

	#mainPage {
		display: table;
		width:100%;
		
		background:url(/assets/img/stars.jpg) repeat top center;
		position:absolute;
		top:0;
		left:0;
		right:0;
		bottom:0;
		height:100%;
	}

	#TOC-container-left {
		display:table-cell;
		width:15%;
	}
	
	#TOCleft { width:14%; }
	#TOCtop { display:none;	}

	#article-body {
		width:800px;  /*fixed width on wide screens*/
		margin:10px;
		border-radius:10px;
	}
	
	@media screen and (max-width:1000px) {
		#article-body {	width:auto; margin:0; }
		#TOC-container-left { width:19%; }
		#TOCleft { width:17%; }
	}
	
	@media screen and (max-width:800px) {
		#TOC-container-left { display:none; }
		#TOCtop { display:initial; background-color:red;}
		.MathJax_Display { text-align: left !important; }
	}
	@media screen and (max-width:650px) {
		#article-body .widebreak { display:none;}
	}



/* Things relevant to the Table of Contents */

	.TOC {
		font-family:sans-serif;
		color: #0f54c4;
		font-weight: 700;
		font-size:.95em;	
	}
	
