/* Things relevant to the TryIt buttons */


	.TryIt {
		float:right;
		background-color:green;
		font-weight:bold;
		border-radius: 10px;
		padding:13px;
		cursor:pointer;
		
		position: relative;
		display: inline-block;
		
		text-decoration:none;
		color:white;
	}

	.tooltiptext {
		visibility:hidden;
		width: 130px;
		background-color: black;
		color: #fff;
		text-align: center;
		padding: 5px 0;
		border-radius: 6px;
	 
		/* Position the tooltip text - see examples below! */
		position: absolute;
		z-index: 5;
		
		top: -5px;
		right: 105%;
		
		font-weight:initial;
		font-size:.82em;
		font-family:sans-serif;
	}


	.tooltiptext p { margin:2px; }

	.tooltiptext::after {
		content: " ";
		position: absolute;
		top: 25%;
		left: 100%; /* To the right of the tooltip */
		margin-top: -5px;
		border-width: 10px;
		border-style: solid;
		border-color: transparent transparent transparent black;
	}

	.tryParent {
		position:relative;
		float:right;
	}
	
	/* Show the tooltip text when you mouse over the tooltip container */
	.tryParent:hover .tooltiptext { 	/*.TryIt:hover .tooltiptext {*/
		visibility: visible;
	}
